"use client";
import React, { useState } from "react";
import { Button } from "@/components/ui/button";

const HeroSection: React.FC = () => {
  const [email, setEmail] = useState("");
  const [status, setStatus] = useState<
    "idle" | "loading" | "success" | "error"
  >("idle");
  const [message, setMessage] = useState<string>("");

  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault();
    if (!email || !/^\S+@\S+\.\S+$/.test(email)) {
      setStatus("error");
      setMessage("Enter a valid email.");
      return;
    }

    try {
      setStatus("loading");
      const res = await fetch("/api/subscribe", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, company: "" }),
      });

      if (res.ok) {
        setStatus("success");
        setMessage("You're on the list. Watch your inbox for early access.");
        setEmail("");
      } else {
        const text = await res.text();
        setStatus("error");
        setMessage(text || "Subscription failed. Try again.");
      }
    } catch {
      setStatus("error");
      setMessage("Network error. Try again.");
    }
  };

  return (
    <section className="relative overflow-hidden py-24 md:py-32 h-[calc(100vh-theme(spacing.16))]">
      <div
        className="pointer-events-none absolute left-[-10%] top-10 h-[340px] w-[540px] rounded-full bg-gradient-to-br from-pink-400 via-orange-300 to-yellow-200 opacity-40 blur-3xl"
        aria-hidden="true"
      />
      <div className="flex px-4 md:px-6 justify-center">
        <div className="max-w-3xl">
          <h1 className="text-5xl font-light leading-tight tracking-tight text-foreground md:text-6xl">
            Practical AI Tools.
            <br />
            Open. Verifiable. Built for You.
          </h1>

          <p className="mt-6 max-w-2xl text-base text-muted-foreground">
            Transparent alternatives to closed platforms like Palantir,
            Airtable, and Salesforce.
          </p>

          <form
            onSubmit={handleSubmit}
            className="mt-8 flex max-w-xl items-center gap-3"
          >
            {/* Honeypot */}
            <input
              type="text"
              name="company"
              className="hidden"
              tabIndex={-1}
              autoComplete="off"
            />
            <div className="flex-1">
              <label htmlFor="email" className="sr-only">
                Email address
              </label>
              <input
                id="email"
                name="email"
                type="email"
                inputMode="email"
                autoComplete="email"
                placeholder="you@company.com"
                className="
                                  w-full rounded-full
                                  bg-white/5 hover:bg-white/10
                                  text-white/90 placeholder-white/60
                                  border border-white/20
                                  px-5 py-3 text-sm outline-none
                                  focus:border-transparent focus:ring-2 focus:ring-white/30
                                  backdrop-blur
                                "
                onChange={(e) => setEmail(e.target.value)}
                aria-label="Email address"
                aria-invalid={status === "error"}
              />
            </div>
            <Button
              type="submit"
              className="rounded-full px-6"
              disabled={status === "loading"}
            >
              {status === "loading" ? "Joining..." : "Join the Waitlist"}
            </Button>
          </form>

          <div
            className="mt-3 min-h-[1.25rem] text-sm"
            role="status"
            aria-live="polite"
            aria-atomic="true"
          >
            {status === "success" && (
              <span className="text-green-500">{message}</span>
            )}
            {status === "error" && (
              <span className="text-red-500">{message}</span>
            )}
          </div>

          <p className="mt-3 text-xs text-muted-foreground">
            No spam. Unsubscribe anytime.
          </p>
        </div>
      </div>
    </section>
  );
};

export default HeroSection;
